export const rowImage=Image
let tag=false
  // 重写原生的Image对象，做到监控项目中所有的打点
export function overrideImage(callback){
    if(tag){return}
    tag=true
    window.Image = function (...params) {
      const img = new rowImage(...params);
      let val = '';
      // 劫持对象中的src，当重设src时判断是否是打点请求
      let proxy = new Proxy(img, {
        get(target,prototype){
            return target[prototype]
        },
        set(target,prototype,value){
          if(prototype=='src'){
           if(callback(value)){
            //当为打点信息时，走拦截器逻辑并在此处阻止打点请求
              target[prototype]=''
              return true
           }
          }
          target[prototype]=value
          return true
        }
      })
      return proxy
    }
  }
